<template lang="pug">
  el-table-column(prop="status" label="" align="center" width="90")
    template(slot-scope="scope")
      el-tag(:type="scope.row.status | statusFilter") {{scope.row.status | dictFilter }}
</template>

<script type="text/ecmascript-6">
  export default {
    filters: {
      statusFilter(status) {
        const statusMap = {
          0: 'warning',
          1: 'info',
          2: 'success',
          3: 'danger'
        }
        return statusMap[status]
      },
      dictFilter(value) {
        const valueMap = {
          0: '',
          1: '',
          2: '',
          3: ''
        }
        return valueMap[value]
      }
    }
  }
</script>

<style scoped lang="stylus">

</style>



