<template lang="pug">
  div
    keep-alive
      base-table(bizKey='reimbursementApplyDetail' title='' v-bind:targetURL='targetURL'
      v-bind:bizDialog='bizDialog' v-bind:btnList='btnList' v-bind:toolbarBtnList="toolbarBtnList" v-bind:customToolBar="customToolBar"
      v-bind:isFixedColumn="isFixedColumn" v-bind:jsonStr="jsonStr")
        template(slot="tableColumnSlot")
          el-table-column(label="" align="center")
            el-table-column(prop="startMonth" label="" align="center" width="40")
            el-table-column(prop="startDay" label="" align="center" width="40")
            el-table-column(prop="startTime" label="" align="center" width="40")
            el-table-column(prop="startPlace" label="" align="center" width="80")
          el-table-column(label="" align="center")
            el-table-column(prop="arriveMonth" label="" align="center" width="40")
            el-table-column(prop="arriveDay" label="" align="center" width="40")
            el-table-column(prop="arriveTime" label="" align="center" width="40")
            el-table-column(prop="arrivePlace" label="" align="center" width="80")
          el-table-column(prop="personNumber" label="" align="center" width="40")
          el-table-column(label="" align="center")
            el-table-column(prop="trafficTool" label="" align="center" width="80")
            el-table-column(prop="trafficMoney" label="" align="center" width="60")
          el-table-column(prop="dayNumber" label="" align="center" width="40")
          el-table-column(label="" align="center")
            el-table-column(prop="subsidyStandard" label="" align="center" width="80")
            el-table-column(prop="subsidyMoney" label="" align="center" width="60")
          el-table-column(label="" align="center")
            el-table-column(prop="hotelMoney" label="" align="center" width="60")
            el-table-column(prop="cityTrafficMoney" label="" align="center" width="60")
          el-table-column(prop="totalMoney" label="" align="center" width="80")
</template>

<script type="text/ecmascript-6">
  import DetailFormModel from './detailModel'
  import BaseTable from '@/components/custom/baseTable'
  import {ReimbursementApplyDetailURL, ReimbursementApplyDetailComponent, DetailToolButtonList} from '../config.toml'
  import {registerComponent} from '@/api/register'

  // 
  registerComponent(ReimbursementApplyDetailComponent)

  export default {
    props: {
      reimbursementApplyId: {
        type: Number,
        default: 0
      },
      readonly: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        isFixedColumn: true,
        hasTableSelection: true,
        targetURL: ReimbursementApplyDetailURL,
        bizDialog: [
          {id: 'addDetail', dialog: 'OaReimbursementApplyDetailAdd'},
          {id: 'edit', dialog: 'OaReimbursementApplyDetailAdd'}
        ],
        jsonStr: `{'reimbursementApplyId':` + this.reimbursementApplyId + `}`
      }
    },
    components: {
      BaseTable
    },
    created() {
    },
    mounted() {
    },
    computed: {
      toolbarBtnList() {
        if (this.readonly) {
          return [{
            id: 'add', isShow: false
          }, {
            id: 'addDetail', title: '', isShow: false, icon: 'icon-add'
          }]
        } else {
          return [{
            id: 'add', isShow: false
          }, {
            id: 'addDetail', title: '', isShow: true, icon: 'icon-add'
          }]
        }
      },
      btnList() {
        if (this.readonly) {
          return []
        } else {
          return DetailToolButtonList
        }
      }
    },
    methods: {
      customToolBar(btnId, table) {
        switch (btnId) {
          case 'addDetail': {
            this.onAddDetail(table)
            break
          }
        }
      },
      onAddDetail(table) {
        let dig =
          table.bizDialog.filter((item) => {
            return item.id === 'addDetail'
          })
        table.whichBizDialog = dig[0].dialog
        setTimeout(() => {
          this.detailFormModel = Object.assign({}, DetailFormModel)
          this.detailFormModel.reimbursementApplyId = this.reimbursementApplyId
          if (this.reimbursementApplyId === 0) {
            alert('')
            return
          }
          table.$refs.kalixDialog.$refs.kalixBizDialog.open('', false, this.detailFormModel)
        }, 20)
      }
    }
  }
</script>

<style scoped lang="stylus">

</style>



