<template lang="pug">
  div.el-form
    el-form-item.s-flex_item(label="" prop="xm" v-bind:label-width="labelWidth")
      el-input(v-model="formModel.xm" readonly)
    div.s-flex
      el-form-item.s-flex_item(label="" prop="dateFirst" v-bind:label-width="labelWidth")
        kalix-date-picker(v-model="formModel.dateFirst" style="width:100%" readonly)
      el-form-item.s-flex_item(label="" prop="dateSecond" v-bind:label-width="labelWidth")
        kalix-date-picker(v-model="formModel.dateSecond" style="width:100%" readonly)
    div.s-flex
      el-form-item.s-flex_item(label="" prop="interviewerFirst" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.interviewerFirst" readonly)
      el-form-item.s-flex_item(label="" prop="interviewerSecond" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.interviewerSecond" readonly)
    div.s-flex
      el-form-item.s-flex_item(label="" prop="interviewContentFirst" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.interviewContentFirst" readonly)
      el-form-item.s-flex_item(label="" prop="interviewContentSecond" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.interviewContentSecond" readonly)
    div.s-flex
      el-form-item.s-flex_item(label="" prop="passFirst" v-bind:label-width="labelWidth")
        // el-checkbox(v-model="formModel.passFirst" disabled) 
        el-switch.s-flex_item(v-model="formModel.passFirst" active-text="" inactive-text="" disabled)
      el-form-item.s-flex_item(label="" prop="passSecond" v-bind:label-width="labelWidth")
        // el-checkbox(v-model="formModel.passSecond" disabled) 
        el-switch.s-flex_item(v-model="formModel.passSecond" active-text="" inactive-text="" disabled)
</template>

<script type="text/ecmascript-6">
  import DatePicker from '@/components/biz/date/datepicker.vue'
  import {InterviewURL} from '../config.toml'
  import Vue from 'vue'

  export default {
    props: {
      formModel: {},
      isRequest: {
        type: Boolean,
        default: false
      },
      bizId: null
    },
    data() {
      return {
        labelWidth: '110px'
      }
    },
    components: {
      KalixDatePicker: DatePicker
    },
    mounted() {
      if (this.isRequest) {
        this.getBizData()
      }
    },
    watch: {
      bizId(newVal) {
        if (this.isRequest) {
          this.getBizData()
        }
      }
    },
    methods: {
      // 
      getBizData() {
        if (this.bizId) {
          // let bizDataUrl = InterviewURL + '/' + this.bizId
          let bizDataUrl = InterviewURL
          let params = {
            params: {
              'jsonStr': {'candidateId': this.bizId},
              'page': 1,
              'limit': 10
            }
          }
          Vue.axios.get(bizDataUrl, params).then((response) => {
            if (response.data.success === undefined) {
              if (response.data.totalCount > 0) {
                this.$emit('update:formModel', response.data.data[0]) // sync
              }
            }
          })
        }
      }
    }
  }
</script>



