import {OaWorkflowComponent} from '../config.toml'
import Vue from 'vue'
import Message from 'common/message'
import {ON_REFRESH_DATA, ON_INIT_DIALOG_DATA} from '@/components/custom/event.toml'
import EventBus from 'common/eventbus'

const _import = require('@/api/_import_' + process.env.NODE_ENV)

const registerComp = () => {
// 
  OaWorkflowComponent.forEach((item) => {
    console.log('[kalix]-[oa] registry name is: ' + item.name, '; registry path is: ' + item.path)
    Vue.component(item.name, _import(item.path))
  })
}

/**
 * 
 * @param scope
 * @returns {boolean}
 */
const startedCond = (scope) => {
  return scope.row.status === 1
}

/**
 * 
 * @param scope
 * @returns {boolean}
 */
const unstartCond = (scope) => {
  return scope.row.status === 0
}

/**
 * 
 * @type {[null,null,null,null,null]}
 */
const workflowBtnList = [
  {
    id: 'view',
    title: '',
    isShow: true,   // 
    isPermission: true  // 
  },
  {
    id: 'edit',
    title: '',
    isShow: true,   // 
    isPermission: true,  // 
    cond: unstartCond
  },
  {
    id: 'delete',
    title: '',
    isShow: true,   // 
    isPermission: true,  // 
    cond: unstartCond
  },
  {
    id: 'progress',
    title: '',
    isShow: true,   // 
    isPermission: true,  // 
    toolTipTitle: '',
    cond: startedCond
  },
  {
    id: 'start',
    title: '',
    isShow: true,   // 
    isPermission: true,  // 
    toolTipTitle: '',
    cond: unstartCond
  },
  {
    id: 'attachment',
    title: '',
    isShow: true,   // 
    isPermission: true,  // 
    toolTipTitle: '',
    cond: unstartCond
  }
]

const startFun = (row, btnId, requestUrl, that) => {
  that.$confirm('?', '', {
    confirmButtonText: '',
    cancelButtonText: '',
    type: 'warning'
  }).then(() => {
    return Vue.axios.request({
      method: 'GET',
      url: requestUrl + row.id
    })
  }).then((res) => {
    Message.processResult(res)
    EventBus.$emit(ON_REFRESH_DATA)
  })
}

const progressFun = (row, btnId, requestUrl, that) => {
  EventBus.$emit('processTask' + '-' + ON_INIT_DIALOG_DATA, row)
  that.$refs.kalixDialog.$refs.kalixBizDialog.open('')
}

const customTableTool = (row, btnId, requestUrl, that) => {
  switch (btnId) {
    case 'start': { // 
      startFun(row, btnId, requestUrl, that)
      break
    }
    case 'progress' : {
      progressFun(row, btnId, requestUrl, that)
      break
    }
  }
}

/**
 * 
 * @type {[null,null,null,null,null]}
 */
const uneditableWorkflowBtnList = [
  {
    id: 'view',
    title: '',
    isShow: true,   // 
    isPermission: true  // 
  },
  {
    id: 'progress',
    title: '',
    isShow: true,   // 
    isPermission: true,  // 
    cond: startedCond
  },
  {
    id: 'start',
    title: '',
    isShow: true,   // 
    isPermission: true,  // 
    cond: unstartCond
  },
  {
    id: 'attachment',
    title: '',
    isShow: true,   // 
    isPermission: true,  // 
    cond: unstartCond
  }
]

export {registerComp, workflowBtnList, customTableTool, uneditableWorkflowBtnList, progressFun, startFun}



