<template lang="pug">
  kalix-dialog.user-add(bizKey="dict"
  ref="kalixBizDialog" v-bind:formModel.sync="formModel" v-bind:isView="readonly"
  )
    div.el-form(slot="dialogFormSlot")
      el-form-item(label="" prop="type")
        el-input(v-model="formModel.type" v-bind:readonly="readonly" auto-complete="off")
      el-form-item(label="" prop="label")
        input(type="hidden" v-model="formModel.id")
        el-input(v-model="formModel.label"  v-bind:readonly="readonly" placeholder="" auto-complete="off")
      el-form-item(label="")
        el-input(v-model="formModel.description" v-bind:readonly="readonly" type="textarea" v-bind:rows="3" placeholder="")
</template>

<script type="text/ecmascript-6">
  import Dialog from '@/components/custom/baseDialog.vue'
  import {FormModel} from './model.toml'
//  import {CommonDictURL} from '../config.toml'

  export default {
    data() {
      return {
        formModel: Object.assign({}, FormModel),
        readonly: true
      }
    },
    components: {
      KalixDialog: Dialog
    },
    methods: {
    }
  }
</script>




