<template lang="pug">
  div.el-form
    div.s-flex
      el-form-item.s-flex_item(label="" prop="personCategory" v-bind:label-width="labelWidth")
        el-input(v-bind:value="formModel.personCategory|getDictName('OA','')" readonly)
      el-form-item.s-flex_item(label="" prop="major" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.major" readonly)
    div.s-flex
      el-form-item.s-flex_item(label="" prop="orgName" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.orgName" readonly)
      el-form-item.s-flex_item(label="" prop="education" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.education" readonly)
    div.s-flex
      el-form-item.s-flex_item(label="" prop="xm" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.xm" readonly)
      el-form-item.s-flex_item(label="" prop="residence" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.residence" readonly)
    div.s-flex
      el-form-item.s-flex_item(label="" prop="sex" v-bind:label-width="labelWidth")
        el-input(v-bind:value="formModel.sex|getDictName('OA','')" readonly)
      el-form-item.s-flex_item(label="" prop="position" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.position" readonly)
    div.s-flex
      el-form-item.s-flex_item(label="" prop="age" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.age" readonly)
      el-form-item.s-flex_item(label="" prop="salary" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.salary" readonly)
    div.s-flex
      el-form-item.s-flex_item(label="" prop="tel" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.tel" readonly)
      el-form-item.s-flex_item(label="" prop="experience" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.experience" readonly)
    div.s-flex
      el-form-item.s-flex_item(label="" prop="graduateSchool" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.graduateSchool" readonly)
      el-form-item.s-flex_item(label="" prop="comment" v-bind:label-width="labelWidth")
        el-input(type="textarea" v-model="formModel.comment" readonly)
</template>

<script type="text/ecmascript-6">
  import {CandidateURL} from '../config.toml'
  import Vue from 'vue'

  export default {
    props: {
      formModel: {},
      isRequest: {
        type: Boolean,
        default: false
      },
      bizId: null
    },
    data() {
      return {
        labelWidth: '110px'
      }
    },
    components: {},
    mounted() {
      if (this.isRequest) {
        this.getBizData()
      }
    },
    watch: {
      bizId(newVal) {
        if (this.isRequest) {
          this.getBizData()
        }
      }
    },
    methods: {
      // 
      getBizData() {
        if (this.bizId) {
          let bizDataUrl = CandidateURL + '/' + this.bizId
          Vue.axios.get(bizDataUrl).then((response) => {
            // this.formModel = response.data
            if (response.data.success === undefined) {
              this.$emit('update:formModel', response.data) // sync
            }
          })
        }
      }
    }
  }
</script>



