<template lang="pug">
  el-table-column(prop="name" v-bind:label="title" v-bind:width="width" align="center")
    template(slot-scope="scope")
      el-tag(v-if="scope.row[name]") {{ scope.row[name] }}
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      name: {
        type: String,
        default: 'businessNo'
      },
      title: {
        type: String,
        default: ''
      },
      width: {
        type: String,
        default: '280'
      }
    }
  }
</script>

<style scoped lang="stylus">

</style>



