<template lang="pug">
  div.el-form
    div.s-flex
      div.s-flex_item
        el-form-item(label="" prop="xm" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.xm" readonly)
        el-form-item(label="" prop="subject" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.subject" readonly)
      el-form-item.s-flex_item(label="" prop="qa" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.qa" type="textarea" v-bind:rows=4 readonly)
    div.s-flex
      div.s-flex_item
        el-form-item(label="" prop="content" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.content" readonly)
        el-form-item(label="" prop="argumentScore" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.argumentScore" readonly)
      el-form-item.s-flex_item(label="" prop="comment" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.comment" type="textarea" v-bind:rows=4 readonly)
    div.s-flex
      div.s-flex_item
        el-form-item(label="" prop="coursewareScore" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.coursewareScore" readonly)
        el-form-item(label="" prop="expressionScore" v-bind:label-width="labelWidth")
          el-input(v-model="formModel.expressionScore" readonly)
      el-form-item.s-flex_item(label="" prop="suggestion" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.suggestion" type="textarea" v-bind:rows=4 readonly)
    div.s-flex
      el-form-item.s-flex_item(label="" prop="attitudeScore" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.attitudeScore" readonly)
      el-form-item.s-flex_item(label="" prop="participant" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.participant" readonly)
    div.s-flex
      el-form-item.s-flex_item(label="" prop="effectScore" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.effectScore" readonly)
      el-form-item.s-flex_item(label="" prop="pass" v-bind:label-width="labelWidth")
        // el-checkbox(v-model="formModel.pass" disabled) 
        el-switch.s-flex_item(v-model="formModel.pass" active-text="" inactive-text="" disabled)
</template>

<script type="text/ecmascript-6">
  import {LectureURL} from '../config.toml'
  import Vue from 'vue'

  export default {
    props: {
      formModel: {},
      isRequest: {
        type: Boolean,
        default: false
      },
      bizId: null
    },
    data() {
      return {
        labelWidth: '110px'
      }
    },
    components: {},
    mounted() {
      if (this.isRequest) {
        this.getBizData()
      }
    },
    watch: {
      bizId(newVal) {
        if (this.isRequest) {
          this.getBizData()
        }
      }
    },
    methods: {
      // 
      getBizData() {
        if (this.bizId) {
          // let bizDataUrl = LectureURL + '/' + this.bizId
          let bizDataUrl = LectureURL
          let params = {
            params: {
              'jsonStr': {'candidateId': this.bizId},
              'page': 1,
              'limit': 10
            }
          }
          Vue.axios.get(bizDataUrl, params).then((response) => {
            if (response.data.success === undefined) {
              if (response.data.totalCount > 0) {
                this.$emit('update:formModel', response.data.data[0]) // sync
              }
            }
          })
        }
      }
    }
  }
</script>



