<template lang="pug">
  div.el-form.kalix-form-table(slot="dialogFormSlot")
    div.table-title 
    div
      el-form-item.kalix-form-table-td(label="" prop="title" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.title" readonly)
          el-button(type="primary" icon="el-icon-search" slot="append" v-on:click="onPreview") 
    div
      el-form-item.kalix-form-table-td(label="" prop="creationDate" v-bind:label-width="labelWidth")
        kalix-date-time-picker(v-model="formModel.creationDate" style="width:100%" readonly)
    div.s-flex
      el-form-item.s-flex_item.kalix-form-table-td(label="" prop="orgName" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.orgName" readonly)
      el-form-item.s-flex_item.kalix-form-table-td(label="" prop="applyDate" v-bind:label-width="labelWidth")
        kalix-date-time-picker(v-model="formModel.applyDate" style="width:100%" readonly)
    div.s-flex
      el-form-item.s-flex_item.kalix-form-table-td(label="" prop="businessNo" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.businessNo")
      el-form-item.s-flex_item.kalix-form-table-td(label="" prop="docType" v-bind:label-width="labelWidth")
        el-input(v-bind:value="formModel.docType|getDictName('OA','')" readonly)
    div.s-flex
      el-form-item.s-flex_item.kalix-form-table-td(label="" prop="docStatus" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.docStatus" readonly)
      el-form-item.s-flex_item.kalix-form-table-td(label="" prop="createBy" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.createBy" readonly)
    div
      el-form-item.kalix-form-table-td(label="" prop="docContent" v-bind:label-width="labelWidth")
        div.doc-content(v-html="formModel.docContent")
    div
      el-form-item.kalix-form-table-td(label="" prop="needHeader" v-bind:label-width="labelWidth")
        div(style="text-align:center")
          el-switch(v-model="formModel.needHeader" active-text="" inactive-text="" disabled)
    template(v-if="formModel.needHeader")
      div
        el-form-item.kalix-form-table-td(label="" prop="needManagerUser" v-bind:label-width="labelWidth")
          <!--kalix-checkbox(v-model="formModel.managerUser" v-bind:dataUrl="managerUsersURL" disabled)-->
          el-input(v-model="formModel.needManagerUser" readonly)
    div.s-flex
      el-form-item.s-flex_item.kalix-form-table-td(label="" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.depUser" readonly)
      el-form-item.s-flex_item.kalix-form-table-td(label="" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.schoolUser" readonly)
    div.s-flex
      el-form-item.s-flex_item.kalix-form-table-td(label="" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.managerUser" readonly)
      el-form-item.s-flex_item.kalix-form-table-td(label="" v-bind:label-width="labelWidth")
        el-input(v-model="formModel.chairmanUser" readonly)
</template>

<script type="text/ecmascript-6">
  import DateTimePicker from '@/components/biz/date/datetimepicker.vue'
  import {RedheadApplyURL} from '../config.toml'
  import {baseURL} from 'config/global.toml'
  import Message from 'common/message'
  import Vue from 'vue'

  export default {
    props: {
      formModel: {},
      isRequest: {
        type: Boolean,
        default: false
      },
      bizId: null
    },
    data() {
      return {
        labelWidth: '140px'
      }
    },
    components: {
      KalixDateTimePicker: DateTimePicker
    },
    mounted() {
      if (this.isRequest) {
        this.getBizData()
      }
    },
    watch: {
      bizId(newVal) {
        if (this.isRequest) {
          this.getBizData()
        }
      }
    },
    methods: {
      // 
      getBizData() {
        if (this.bizId) {
          let bizDataUrl = RedheadApplyURL + '/' + this.bizId
          let params = {
            params: {}
          }
          Vue.axios.get(bizDataUrl, params).then((response) => {
            if (response.data.success === undefined) {
              if (response.data) {
                this.$emit('update:formModel', response.data) // sync
              }
            }
          })
        }
      },
      // 
      onPreview() {
        if (this.bizId) {
          window.open(baseURL + '/camel/servlet/download?beanname=RedheadApply&id=' + this.bizId + '&filetype=html')
        } else {
          if (this.formModel.id) {
            window.open(baseURL + '/camel/servlet/download?beanname=RedheadApply&id=' + this.formModel.id + '&filetype=html')
          } else {
            Message.warning(',!')
          }
        }
      }
    }
  }
</script>
<style scoped lang="stylus" type="text/stylus">
  .doc-content
    max-height 500px
    overflow auto
</style>



